* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #e5eeda;
  color: #212121;
  line-height: 1.5;
  display: flex;
  justify-content: center;
}

#search-box {
  font: inherit;
  padding: 10px;
  width: 400px;
  height: 40px;
  margin-top: 100px;
  text-align: center;
  border: 2px solid #000080;
  border-radius: 10px;
}

#search-box:hover:focus {
  box-shadow: 0 0 15px 4px rgba(0, 0, 0, 0.06);
  background-color: #fafafa;
}

.country {
  padding: 0;
  display: flex;
  align-items: center;
}

.countries {
  display: flex;
  align-items: center;
}

.country__name {
  font-size: 30px;
  font-weight: 400;
  margin-left: 20px;
}

.country__description {
  font-size: 20px;
  font-weight: 300;
  margin-top: 10px;
}

.country__images {
  display: block;
  padding: 15px 0;
}
